export default {
	data() {
		return {
			current: 0,
			tab: [{
				name: "流水明细",
			}, {
				name: "充值明细",
			}],
			lists: []
		}
	},
	methods: {
		tabChange: function(e) {
			var index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.current = index;
		},
		swiperChange: function(e) {
			var index = e.target.current || e.detail.current;
			this.current = index;
		}
	},
	onLoad() {
		let list = [];
		for (let i = 0; i < 20; i++) {
			list.push(i)
		}
		this.lists = list;
	}
}
